import React, { useState, useEffect } from 'react';
import articleConent from './article-content.js'
import CommentsList from '../components/commentsList.js';
import ArticleList from '../components/ArticleList.js';
import Upvotes from '../components/UpVotes-section';
import AddComponentForm from '../components/AddCommentForm';
import NotFoundPage from './NotFoundPage.js'


const Article = ({ match }) => {

    const [ArticleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });
    const name = match.params.name;
    const article = articleConent.find(article => article.name === name);

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/artcile/${name}`);
            const body = await result.json();
            console.log(body);
            setArticleInfo(body);
        }
        fetchData();
    }, [name]);

    if (!article) {
        return (<NotFoundPage />)
    }

    const other = articleConent.filter(article => (article.name !== name));

    return (
        <>
            <h1>{article.title}</h1>
            <Upvotes upvote={ArticleInfo.upvotes} articleName={name} setArticleInfo={setArticleInfo}/>
            {article.content.map((para, key) => (
                <p key={key}>{para}</p>
            ))}
            <CommentsList comments={ArticleInfo.comments}/>
            <AddComponentForm articleName={name} setArticleInfo={setArticleInfo}/>
            <h3>Other Articles : </h3>
            <ArticleList articles={other} />
        </>
    );
}

export default Article;