import React from 'react'

const Upvotes = ({upvote, articleName, setArticleInfo}) => {

    const doUpvote = async () => {
        const result = await fetch(`/api/artcile/${articleName}/upvote`,{
            method: 'post',
        });
        const body = await result.json();
        setArticleInfo(body);
    }
    return (
        <div className="upvotes-section">
            <button onClick={() => doUpvote()}>Add upvote</button>
            <p>This article has been upvoted {upvote} times</p>
        </div>
    );
}

export default Upvotes;