import React, { useState } from 'react'

const AddComponentForm = ({ articleName, setArticleInfo }) => {

    const [userName, setUserName] = useState('');
    const [commentText, setCommentText] = useState('');

    const Addcomment = async () => {

        const result = await fetch(`/api/artcile/${articleName}/add-comment`, {
            method: 'post',
            body: JSON.stringify({
                user: userName, text: commentText
            }),
            headers: {
                'Content-Type': 'application/json',
            }
        });

        const body = await result.json();

        setArticleInfo(body);
        setUserName('');
        setCommentText('');

    };

    const inputStyle = {
        border: '2px solid black',
        padding: '8px',
        borderRadius: '8px',
    }

    return (
        <>
            <div className="add-comment-form">
                <h3>Add a Comment</h3>
                <label>Name:
                <input type="text" value={userName} onChange={(event) => setUserName(event.target.value)} style={inputStyle} />
                </label>
                <br></br>
                <label>Comment:
                <textarea rows="4" cols="50" value={commentText} onChange={(event) => setCommentText(event.target.value)} />
                </label>
                <button onClick={() => Addcomment()}>Add Comment</button>
            </div>
        </>
    );
}

export default AddComponentForm;